import React,{useState} from 'react'


export default function Textform(props) {
     const handleUpClick = () => {
        //  console.log("UpperCasebtn was clicked");
         let newText = text.toUpperCase();
         setText(newText)
         props.showAlert("Converted to UPPERCASE! ", "success");
     }
     const handleLowClick = () => {

         let newText = text.toLowerCase();
         setText(newText)
         props.showAlert("Converted to lowercase! ", "success");
     }
     const handleClrClick = () => {
         setText("")
         props.showAlert("You have cleared all text! ", "success");
     }
     const handleUndoClick = () => {
        const Splitted = text.split(" ");
        let newText = Splitted.slice(0,-1);  
         setText(newText.join(" "))
         props.showAlert("you have Undo something!", "warning");
    }
    const handlePstClick = () => {
        const newText = text;
        navigator.clipboard.readText()
  .then(text => {
    setText(text + " " + newText);
    props.showAlert("You have pasted something! ", "success");
  })
  .catch(err => {
    console.error('Failed to read clipboard contents: ', err);
  });
  
    }
    const handleXtrClick = () => {
        let newText = text.split(/[  ]+ /);  
         setText(newText.join(" "))
         props.showAlert("Extra Spaces have been removed!", "success");
    }
   
     const handleOnchange = (event) => {
        // console.log("called handleOnchange");
        setText(event.target.value);
    }

    const [text, setText] = useState("");
  return (
<>
<div className="container" style={{color: props.mode==='dark'?'white':'#042743'}}> 
    <h1>{props.heading}</h1>
    <div className="mb-3">
    <textarea className="form-control" placeholder='Enter the text' value = {text} onChange={handleOnchange} style={{backgroundColor: props.mode==='dark'?'#042743':'white' , color: props.mode==='dark'?'white':'#042743'}} id="myBox" rows="8" ></textarea>
    <button className="btn btn-primary mx-2 my-2" onClick={handleClrClick}>Clear text</button>
    <button className="btn btn-primary my-2" onClick={handleUpClick}>Convert to UPPERCASE</button>
    <button className="btn btn-primary mx-2 my-2" onClick={handleLowClick}>Convert to lowercase</button>
    <button className="btn btn-primary mx-2 my-2" onClick={handleUndoClick}>Undo</button>
    <button className="btn btn-primary mx-2 my-2" onClick={() => {navigator.clipboard.writeText(text); props.showAlert("Copied Successfully ", "success");}}>Copy</button>
    <button className="btn btn-primary mx-2 my-2" onClick={handlePstClick}>Paste</button>
    <button className="btn btn-primary mx-2 my-2" onClick={handleXtrClick}>Remove Xtra Spaces</button>
    </div>
</div>
<div className="container my-3" style={{color: props.mode==='dark'?'white':'#042743'}}> 
<h2>Text Summary :</h2>
<p> {text.split(" ").length } words and {text.length} characters</p>
<p> {0.008 * text.split(" ").length} minutes to read</p>
<h2>Preview</h2>
<p>{text.length>0?text:'Ente some text to preview it!'}</p>
</div>
</>

  )
}
